@import url(https://fonts.googleapis.com/css2?family=Comic+Neue:wght@300;400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Megrim&display=swap);
:root{
    --purple:#814096;
    --pink:#F83292;
    --gradient:linear-gradient(90deg, var(--purple), var(--pink));
}
::-webkit-scrollbar{
    display: none;
}

*{
    margin:0; 
    padding:0;
    box-sizing: border-box;
    text-decoration: none;
    outline: none; 
    border:none;
    text-transform: capitalize;
}

html{
    font-size: 62.5%;
    overflow-x: hidden;
}

.section{
    min-height: 100vh;
    padding:0 9%;
    padding-top: 7.5rem;
    padding-bottom: 2rem;
}

.btn{
    display: -webkit-inline-flex;
    display: inline-flex;
    margin-top: 1rem;
    padding:.8rem 3rem;
    border-radius: 5rem;
    background:linear-gradient(90deg, #814096, #F83292);
    background:var(--gradient);
    font-size: 1.7rem;
    color:#fff;
    cursor: pointer;
    box-shadow: 0.5rem 0.6rem 1rem rgba(0,0,0,0.5);
    transition: all .3s linear;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-family: cursive;
}

.btn:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

@-webkit-keyframes float{
    0%, 100%{
        -webkit-transform: translateY(0rem);
                transform: translateY(0rem);
    }
    50%{
        -webkit-transform: translateY(-3.5rem);
                transform: translateY(-3.5rem);
    }
}

@keyframes float{
    0%, 100%{
        -webkit-transform: translateY(0rem);
                transform: translateY(0rem);
    }
    50%{
        -webkit-transform: translateY(-3.5rem);
                transform: translateY(-3.5rem);
    }
}

.about{
    background:url(/codeScanner/static/media/about-bg.38fdb5e5.png) no-repeat;
    background-size: cover;
    background-position: center;
    padding-bottom: 3rem;
}

.about .column{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.about .column .image{
    -webkit-flex:1 1 40rem;
            flex:1 1 40rem;
}

.about .column .image img{
    width:100%;
    -webkit-animation: float 3s linear infinite;
            animation: float 3s linear infinite;
}

.about .column .content{
    -webkit-flex:1 1 40rem;
            flex:1 1 40rem;
}

.about .column .content h3{
    font-size: 3.5rem;
    color:#111;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-family: 'Megrim', cursive;
    text-shadow: 5px 5px #999;
}

.about .column .content p{
    font-size: 1.7rem;
    color:#666;
    padding:1rem 0;
    font-family: 'Comic Neue', cursive;
    font-weight: 600;
}

.about .column .content .buttons a:last-child{
    margin-left: 2rem;
}


/* media queries  */

@media (max-width:1200px){
    
    html{
        font-size: 55%;
    }

}

@media (max-width:991px){
    
    .section{
        padding:0 3%;
        padding-top: 7.5rem;
        padding-bottom: 2rem;
    }

}

@media (max-width:768px){
    .about{
        background-position: right;
    }
}

@media (max-width:450px){
    html{
        font-size: 50%;
    }

    .about .column .content .buttons a{
        width:100%;
        text-align: center;
    }

    .about .column .content .buttons a:last-child{
        margin: 1rem 0;
    }
}
